/**
 * Creates an [`ImagePaint`](https://figma.com/plugin-docs/api/Paint/#imagepaint)
 * object from the `bytes` of an image.
 *
 * @category Image
 */
export function createImagePaint(bytes) {
    const image = figma.createImage(bytes);
    return {
        imageHash: image.hash,
        scaleMode: 'FILL',
        scalingFactor: 0.5,
        type: 'IMAGE'
    };
}
